.header {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    width:100%;
    height: 44px;
    padding:0px 18px;

    border-bottom: 1px solid #dbdbdb;
    background-color: #fff;
}

button{
    padding: 0;
    border:0;
    background: none;
    font-size: 100%;
    font-family: inherit;
  }
  button:focus{
    outline: none;
  }

.headerItem{
  padding-left: 110px;
}

.goBack{
  padding: 0;
  margin: 0;
  float: left;
}